.statistics {
    margin-top: 5rem;
    &_info {
        justify-content: space-evenly;
        border-radius: 20px;
        background-color: #ffffff;
        padding: 4rem 0;
        .card {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            margin: 0 20px;
            .percent {
                position: relative;
                width: 77px;
                height: 8rem;
                .dot {
                    content: "";
                    position: absolute;
                    top: 49%;
                    left: -2px;
                    width: 7px;
                    height: 7px;
                    border-radius: 50%;
                    border: 2px solid #4A3274;
                    background-color: white;
                    z-index: 1;
                }
            }
            svg {
                position: relative;
                width: 100%;
                height: 100%;
                transform: rotate(-180deg);
                circle {
                    width: 100%;
                    height: 100%;
                    fill: none;
                    stroke: #f0f0f0;
                    stroke-width: 2px;
                    &:last-of-type {
                        stroke-dasharray: 232px;
                        stroke-dashoffset: calc(232px - (232px * (var(--percent)) / 100));
                        stroke: #4A3274;
                    }
                }
            }
            .number {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                h3 {
                    font-weight: 500;
                    font-size: 2rem;
                }
            }
            .title {
                text-align: center;
                font-size: 1.6rem;
                color: $textBlack;
                h3 {
                    font-weight: 500;
                    margin: 1.5rem 0 1rem;
                }
                p {
                    font-weight: 400;
                    margin: 0;
                }
            }
            &:nth-child(2) {
                svg circle:last-of-type {
                    stroke: #739500;
                }
                .number {
                    color: #739500;
                }
                .dot {
                    border-color: #739500;
                }
            }
            &:nth-child(3) {
                svg circle:last-of-type {
                    stroke: #950086;
                }
                .number {
                    color: #950086;
                }
                .dot {
                    border-color: #950086;
                }
            }
        }
    }
}

.homework {
    margin-top: 5rem;
    &_chart {
        background: white;
        border-radius: 12px;
        padding: 4rem;
        height: 400px;
        #canvas {
            @media (max-width: 782px) {
                height: 250px !important;
            }
        }
        @media (max-width: 782px) {
            padding: 0;
            height: auto;
            padding-bottom: 3rem;
        }
    }
    .btn {
        border-color: #E8E8E8;
        background-color: #fff;
        font-weight: 500;
        i {
            margin-right: 0;
            margin-left: .5rem;
        }
    }
}

.Attendence {
    margin-top: 5rem;
    .month {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 20px;
        font-weight: 300;
        font-size: 1.8rem;
        &_nav {
            display: flex;
            justify-content: center;
            align-items: center;
            text-decoration: none;
            background-color: white;
            color: #026BB0;
            width: 38px;
            height: 38px;
            border-radius: 25%;
            transition-duration: .2s;
            position: relative;
        }
        .monthYear_name {
            margin: 0 1rem;
        }
    }
    &_calendar {
        width: 100%;
        max-width: 1055px;
        justify-content: center;
        align-items: center;
        background: #fff;
        padding: 3rem 2.5rem 2rem;
        border-radius: 20px;
        .days {
            display: grid;
            justify-content: center;
            align-items: center;
            grid-template-columns: repeat(7, 1fr);
            color: #999;
            font-weight: 600;
            margin-bottom: 15px;
            span {
                width: 100%;
                justify-self: center;
                align-self: center;
                text-align: center;
                font-size: 1.4rem;
            }
        }
        .dates {
            display: grid;
            grid-template-columns: repeat(7, 1fr);
            button {
                cursor: pointer;
                outline: 0;
                border: 0;
                border-radius: 5px;
                background: rgba(#78B517, .1);
                font-size: 1.8rem;
                justify-self: center;
                align-self: center;
                width: 97%;
                height: 50px;
                margin-bottom: 5px;
                color: $textBlack;
                transition-duration: .2s;
                font-weight: 400;
                &:first-child {
                    grid-column: 3;
                }
                &.pink {
                    background-color: rgba(#E51D1D, .1);
                }
                &.grey {
                    background-color: rgba(#333333, .1);
                }
            }
        }
    }
}

.gradebook {
    margin-top: 4rem;
    color: $textBlack;
    overflow: scroll;
    &_container {
        display: flex;
        min-width: 1000px;
    }
    &_col {
        width: 19%;
        &:not(:last-of-type) {
            margin-right: .3rem;
        }
        &:first-of-type {
            width: 24%;
            span {
                font-weight: 500;
            }
            .gradebook_col_list {
                text-align: start;
            }
        }
        h2,
        h4 {
            margin-bottom: 2rem;
        }
        h4 {
            font-weight: 400;
            text-align: center;
        }
        &_list {
            background-color: white;
            display: flex;
            flex-direction: column;
            border-radius: 20px;
            border: 1px solid #E9E9E9;
            text-align: center;
            &>span {
                font-size: 1.6rem;
                margin: 0 2rem;
                padding: 2rem;
                .red {
                    color: #FF0000;
                    font-weight: 400;
                }
                &:not(:last-of-type) {
                    border-bottom: 1px solid #E9E9E9;
                }
            }
        }
    }
    .total_marks {
        min-width: 1000px;
        margin-top: .3rem;
        background-color: white;
        padding: 2rem 4rem;
        border-radius: 20px;
        border: 1px solid #E9E9E9;
        h2,
        h5 {
            font-size: 2rem;
            margin: 0;
        }
        h2,
        span {
            font-weight: 500;
        }
        h5 {
            font-weight: 400;
        }
    }
}