.d-flex {
    display: flex;
    align-items: center;
}

.jcb {
    justify-content: space-between;
}

.mainHeading {
    font-size: 2rem;
    color: $textBlack;
    text-transform: capitalize;
    position: relative;
    margin-left: 2rem;
    &:after {
        content: "";
        position: absolute;
        top: 50%;
        left: -2rem;
        width: .9rem;
        height: 2.9rem;
        border-radius: 6px;
        background-color: #AE7129;
        transform: translate(0, -50%);
    }
}