.nav {
    padding: 2.1rem 0;
    &_logo {
        img {
            width: 5rem;
            margin-right: 1rem;
        }
        span {
            font-size: 1.4rem;
        }
    }
    &_btns {
        a {
            &:first-of-type {
                margin-right: 3rem;
            }
            i:nth-of-type(1) {
                background-color: #fff;
                color: $colorPrimary;
                padding: 1.7rem;
                border-radius: 1.3rem;
                font-size: 1.7rem;
            }
            i:nth-of-type(2) {
                font-size: 1.7rem;
                margin-left: 1rem;
                color: #959595;
            }
            &.notifications {
                i {
                    position: relative;
                    &:after {
                        content: "";
                        position: absolute;
                        top: 38%;
                        right: 9px;
                        width: 1.3rem;
                        height: 1.3rem;
                        border-radius: 50%;
                        background-color: #ff3c3c;
                        transform: translate(0, -50%);
                    }
                }
            }
        }
    }
}

.profileNav {
    background-color: #fff;
    padding: 1.5rem 0;
    .avatar {
        @media (max-width: 782px) {
            align-self: flex-start;
        }
        img {
            width: 8.4rem;
            margin-right: 1rem;
        }
        h4 {
            font-size: 2.8rem;
            font-weight: 700;
        }
    }
    .childs {
        @media (max-width: 782px) {
            flex-direction: column;
        }
        .child {
            transition: all .3s ease;
            padding: 1rem;
            border: 1px solid #F0F0F1;
            margin-right: 2rem;
            border-radius: 8px;
            display: flex;
            align-items: center;
            cursor: pointer;
            @media (max-width: 782px) {
                margin-bottom: 1rem;
            }
            img {
                width: 3.2rem;
                margin-right: 1rem;
            }
            span {
                font-size: 1.6rem;
                text-transform: capitalize;
                color: #707070;
                font-weight: 600;
                transition: all .3s ease;
            }
            &:hover {
                background-color: rgba($colorPrimary, .1);
                border-color: rgba($colorPrimary, .1);
                span {
                    color: $colorPrimary
                }
            }
        }
    }
}

.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $colorPrimary;
    border-radius: 8px;
    font-size: 1.6rem;
    padding: 1.2rem 2.6rem;
    text-transform: capitalize;
    background-color: transparent;
    color: $colorPrimary;
    font-weight: 600;
    cursor: pointer;
    transition: all .3s ease;
    i {
        margin-right: 1rem;
    }
    &:hover {
        background-color: rgba($colorPrimary, .1)!important;
        border-color: rgba($colorPrimary, .1);
    }
}