*,
 ::after,
 ::before {
    box-sizing: border-box;
}

html {
    overflow-x: hidden;
    font-size: 62.5%;
}

body {
    background-color: #FBFBFC;
    margin: 0;
    font-size: 1.6rem;
}

.container {
    max-width: 1115px;
    margin: 0 auto;
    @media (max-width: 1115px) {
        margin: 0 1rem;
    }
}

button,
a,
input {
    cursor: pointer;
}